@use 'sass:math';

/* // Подключение миксинов ===========================================================================================================================================================================================================================================================================
 */
@import 'SCSS/mixins';

/* // Подключение шрифтов ===========================================================================================================================================================================================================================================================================
//&display=swap - добавить при подключении через плагин */
@import url(https://fonts.googleapis.com/css?family=Bai+Jamjuree:200,200italic,300,300italic,regular,italic,500,500italic,600,600italic,700,700italic&display=swap);
/* // Подключить если есть локальные файлы шрифтов */
@import url(https://fonts.googleapis.com/css?family=Combo:regular&display=swa);
/* // Подключение иконочных шрифтов ==============================================================================================================================================================================================================================================================================
// Подключить если есть файл иконочного шрифта */
@import url(https://fonts.googleapis.com/css?family=Qwitcher+Grypen:regular,700);
@import url(https://fonts.googleapis.com/css?family=Press+Start+2P:regular);
@import url(https://fonts.googleapis.com/css?family=Marck+Script:regular);
/* // Шрифт по умолчанию ==============================================================================================================================================================================================================================================================================
 */
$fontFamily: 'Marck Script';
$fontSize: rem(14); /*  // где 14(px) - размер шрифта по умолчанию из макета */

/* // Основные цвета */
$mainColor: #000; /* // Цвет шрифта по умолчанию */

/* // НАСТРОЙКИ ===============================================================================================================================================================================================================================================================================================
// Минимальная ширина страницы */
$minWidth: 320;
/* // Ширина полотна (макета) */
$maxWidth: 1920;
/* // Ширина ограничивающего контейнера (0 = нет ограничения) */
$maxWidthContainer: 1280;
/* // Общий отступ у контейнера */
/* // (30 = по 15px слева и справа, 0 = нет отступа) */
$containerPadding: 30;

/* // Ширина срабатывания первого брейкпоинта */
$containerWidth: $maxWidthContainer + $containerPadding;

$pc: em(
	$containerWidth
); /* // ПК, ноутбуки, некоторые планшеты в горизонтальном положении */
$tablet: em(991.98);
$mobile: em(767.98);
$mobileSmall: em(479.98);

/* // Тип адаптива: */
// 1 = отзывчивость (у контейнера нет брейкпоинтов),
// 2 = по брейк-поинтам (контейнер меняет свою ширину по брейк-поинтам)
$responsiveType: 1;

@import 'SCSS/null';

body {
	overflow-x: hidden;
	// Скролл заблокирован
	.lock & {
		overflow: hidden;
		touch-action: none;
	}
	// Сайт загружен
	.loaded & {
	}
}
// Оболочка 
.wrapper {
	min-height: 100%;
	display: flex;
	flex-direction: column;
	overflow: hidden;
	background: #f2f1ef;
	// Прижимаем footer
	> main {
		flex: 1 1 auto;
	}
	// Фикс для слайдеров
	> * {
		min-width: 0;
	}
}
// Ограничивающий контейнер ======================================================================================================================================================================================================================

/*
(i) Стили будут применяться ко 
всем классам содержащим *__container
Например header__container, main__container и т.д.
Снипет (HTML): cnt
*/
@if ($responsiveType==1) {
	// Отзывчивая
	[class*='__container'] {
		@if ($maxWidthContainer>0) {
			max-width: rem($maxWidthContainer);
			box-sizing: content-box;
			margin: 0 auto;
		}
		@if ($containerPadding>0) {
			padding: 0 rem(math.div($containerPadding, 2));
		}
	}
} @else {
	// По брейк-поинтам
	[class*='__container'] {
		margin: 0 auto;
		@if ($maxWidthContainer>0) {
			max-width: rem($maxWidthContainer);
			box-sizing: content-box;
		} @else {
			@if ($containerPadding>0) {
				padding: 0 rem(math.div($containerPadding, 2));
			}
		}
		@media (max-width: $pc) {
			max-width: rem(970);
		}
		@media (max-width: $tablet) {
			max-width: rem(750);
		}
		@media (max-width: $mobile) {
			max-width: none;
			@if ($containerPadding>0 and $maxWidthContainer>0) {
				padding: 0 rem(math.div($containerPadding, 2));
			}
		}
	}
}
body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
		'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}
/* width */
::-webkit-scrollbar {
	width: 3px;
}

/* Track */
::-webkit-scrollbar-track {
	background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
	background: rgb(255, 137, 2);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
	background: #888;
}
